          '<p><br></p><h1 style="text-align: left;"></h1><h1 style="text-align: left;"><span style="color: rgb(0, 0, 0);"></span></h1><p style="text-align: left;"><br></p><h1 style="text-align: left;"><span style="color: rgb(26, 26, 26);">XXXX</span></h1><p style="text-align: left;"><br></p><h2 style="text-align: left;"><span style="color: rgb(0, 0, 0);"></span></h2><p style="text-align: left;"><span style="color: rgb(193, 193, 193);"></span></p><ul><li></li><li></li><li></li></ul><p style="text-align: left;"><br></p><p style="text-align: left;"><br></p><h2 style="text-align: left;"><span style="color: rgb(0, 0, 0);"></span></h2><p style="text-align: left;"><span style="color: rgb(193, 193, 193);"></span></p><ul><li></li><li></li><li></li></ul><p style="text-align: left;"><br></p><p style="text-align: left;"><br></p><h2 style="text-align: left;"><span style="color: rgb(0, 0, 0);"></span></h2><p style="text-align: left;"><span style="color: rgb(193, 193, 193);"></span></p><ul><li>... ...</li><li>... ...</li><li>... ...</li></ul><h1 style="text-align: left;"></h1><h1 style="text-align: left;"><span style="color: rgb(26, 26, 26);">XXXX</span></h1><p style="text-align: left;"><br></p><h2 style="text-align: left;"><span style="color: rgb(0, 0, 0);"></span></h2><p style="text-align: left;"><span style="color: rgb(193, 193, 193);"></span></p><ul><li></li><li></li><li></li></ul><p style="text-align: left;"><br></p><p style="text-align: left;"><br></p><h2 style="text-align: left;"><span style="color: rgb(0, 0, 0);"></span></h2><p style="text-align: left;"><span style="color: rgb(193, 193, 193);"></span></p><ul><li></li><li></li><li></li></ul><p style="text-align: left;"><br></p><p style="text-align: left;"><br></p><h2 style="text-align: left;"><span style="color: rgb(0, 0, 0);"></span></h2><p style="text-align: left;"><span style="color: rgb(193, 193, 193);"></span></p><ul><li>... ...</li><li>... ...</li><li>... ...</li></ul><p style="text-align: left;"><br></p><p style="text-align: justify;"><br></p><p style="text-align: left;"><br></p>',
          '<h1 style="text-align: left;"><span style="color: rgb(26, 26, 26);"></span></h1><p style="text-align: left;"><br></p><p style="text-align: left;"><span style="color: rgb(17, 17, 17);">11.12 - 3.21</span></p><p style="text-align: left;"><span style="color: rgb(17, 17, 17);">2</span><span style="color: rgb(30, 111, 255);">@ 1</span><span style="color: rgb(17, 17, 17);"></span><span style="color: rgb(30, 111, 255);">@ 2</span><span style="color: rgb(17, 17, 17);">...</span></p><p style="text-align: left;"><br></p><h2 style="text-align: left;"><span style="color: rgb(17, 17, 17);"></span></h2><p style="text-align: left;"><span style="color: rgb(17, 17, 17);">3</span></p><p style="text-align: left;"><br></p><p style="text-align: left;"><br></p><h2 style="text-align: left;"><span style="color: rgb(17, 17, 17);"></span></h2><p><br></p><table style="width: auto;"><tbody><tr><td colSpan="1" rowSpan="1" width="150"></td><td colSpan="1" rowSpan="1" width="150"></td><td colSpan="1" rowSpan="1" width="150"></td><td colSpan="1" rowSpan="1" width="150"></td></tr><tr><td colSpan="1" rowSpan="1" width="150"></td><td colSpan="1" rowSpan="1" width="150">122</td><td colSpan="1" rowSpan="1" width="150"></td><td colSpan="1" rowSpan="1" width="150"></td></tr><tr><td colSpan="1" rowSpan="1" width="150"></td><td colSpan="1" rowSpan="1" width="150">123</td><td colSpan="1" rowSpan="1" width="150"></td><td colSpan="1" rowSpan="1" width="150"></td></tr><tr><td colSpan="1" rowSpan="1" width="150"></td><td colSpan="1" rowSpan="1" width="150">212</td><td colSpan="1" rowSpan="1" width="150"></td><td colSpan="1" rowSpan="1" width="150"></td></tr><tr><td colSpan="1" rowSpan="1" width="150"></td><td colSpan="1" rowSpan="1" width="150">214</td><td colSpan="1" rowSpan="1" width="150"></td><td colSpan="1" rowSpan="1" width="150"></td></tr><tr><td colSpan="1" rowSpan="1" width="150"></td><td colSpan="1" rowSpan="1" width="150">224</td><td colSpan="1" rowSpan="1" width="150"></td><td colSpan="1" rowSpan="1" width="150"></td></tr></tbody></table><p style="text-align: left;"><br></p><p style="text-align: left;"><br></p><h2 style="text-align: left;"><span style="color: rgb(17, 17, 17);"></span></h2><ol><li></li><li>2</li><li>3</li></ol><p style="text-align: left;"><br></p><p style="text-align: left;"><br></p><h2 style="text-align: left;"><span style="color: rgb(17, 17, 17);"></span></h2><ol><li></li><li>2</li><li>3</li></ol><p style="text-align: left;"><br></p><p style="text-align: left;"><br></p><h2 style="text-align: left;"><span style="color: rgb(17, 17, 17);"></span></h2><p style="text-align: left;"><span style="color: rgb(193, 193, 193);"></span></p><ul><li></li><li></li><li></li></ul><p style="text-align: left;"><br></p><p style="text-align: left;"><br></p><p style="text-align: left;"><br></p><p><br></p>',
      ]
    }
  },
  methods: {
    onCreated(editor) {
      this.editor = Object.seal(editor) //  Object.seal() 
    },
    showMe() {
      this.$emit('getEditorContent',this.html);
    }
  },
  created() {
    this.$nextTick(()=>{
      if(this.docContent!=='') {
        this.html = this.docContent;
      }
    })
  },
  async mounted() {
    if(this.$route.params.isCreate) {
      setTimeout(() => {
        this.html = this.template[this.$route.params.templateType];
      }, 1)
    }
    else {
      this.$forceUpdate();
      console.log(store.state.currentFileId);
      let res =await getDocDetails(store.state.currentFileId,{
        username:store.state.user.username,
      })
      this.html = res.content;
      this.$emit('getDocumentInfo',res)
    }
  },
  async activated() {
    let res =await getDocDetails(store.state.currentFileId,{
      username:store.state.user.username,
    })
    this.html = res.content;
    this.$emit('getDocumentInfo',res)
  },
  beforeDestroy() {
    const editor = this.editor
    if (editor == null) return
    editor.destroy() // 
  }
})
</script>

<style src="@wangeditor/editor/dist/css/style.css"></style>
